$step: 1px;

$icon: 14px;

$width-xs: 320px;
$width-sm: 372px;
$width-md: 640px;
$width-lg: 768px;
$width-xl: 1024px;

$width-md-page-external-dash: 450px;
$width-md-page-external-form: 640px;

$width-xs-ios: 320px; // 5 and below
$width-sm-ios: 375px; // 6, 6s, 7, 8, 10, 11Pro
$width-sm-ios-plus: 414px; // 6+, 6s+, 7+, 8+, 10r, 10s, 11, 11ProMax

$width-menu: 240px;

$height-header-sm: 60px;
$height-header-md: 8.4rem;

$height-xs-ios: 568px; // 5 and below
$height-sm-ios: 667px; // 6, 6s, 7, 8
$height-sm-ios-plus: 736px; // 6+, 6s+, 7+, 8+
$height-sm-ios-pro: 812px; // 10, 11Pro
$height-sm-ios-max: 896px; // 10r, 10s, 11, 11ProMax

@function decrease($size) {
    @return $size + ($step * -1);
}

@function increase($size) {
    @return $size + $step;
}
