@use "@app/bancor-components/Container/BaseExternal";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Element/Form/Input";
@use "@app/bancor-components/Element/Form/InputSelect";
@use "@app/bancor-components/Element/Form/InputNumber";
@use "@app/bancor-components/Element/Form/InputPassword";
@use "@app/bancor-components/Style/screen";
@use "@app/bancor-components/Style/size";
@use "@app/bancor-components/Style/Border";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Padding";

#{BaseExternal.$selector} {
    .textSubtitle {
        #{Text.$selector} {
            @include Color.set($fg: "gray-dark");
            @include Font.set($size: delta, $weight: default, $family: "default", $align: default);
        }
    }

    #{Text.$selector}.byCodeInvitationCode {
        @include Color.set($fg: "gray-dark");
        @include Font.set($size: gamma, $weight: default, $family: "default");

        @include screen.xl {
            @include Flex.set($justify: center);
            @include Font.set($align: center);
        }
    }

    .textHeading {
        #{Text.$selector} {
            @include Margin.set($top: delta);
            @include Color.set($fg: "gray-dark");
            @include Font.set($size: delta, $weight: default, $family: "default", $align: default);
        }
    }

    .alignInputs {
        @include Margin.set($top: delta);

        #{Input.$selector},
        #{InputSelect.$selector},
        #{InputNumber.$selector},
        #{InputPassword.$selector} {
            @include Margin.set($bottom: delta);
        }
    }

    .alignInvitationCode {
        @include Margin.set($top: delta);
        @include Flex.set($justify: center, $align: center);

        #{Text.$selector} {
            @include Margin.set($right: alpha);
            @include Color.set($fg: "gray-dark");
            @include Font.set($size: gamma, $weight: default, $family: "default", $align: default);
        }

        #{Button.$selector} {
            @include Padding.set($preset: none);
            @include Color.set($bg: "transparent");
            @include Border.set($style: default);
            cursor: pointer;

            #{Text.$selector} {
                @include Color.set($fg: "primary");
                @include Font.set(
                    $size: gamma,
                    $weight: medium,
                    $family: "default",
                    $align: default
                );
            }
        }
    }

    .resendCode {
        @include Margin.set($top: delta, $bottom: delta);
        @include Flex.set($justify: center);

        #{Button.$selector} {
            @include Color.set($bg: "transparent");
            @include Padding.set($preset: none);
            @include Border.set($style: default);
            cursor: pointer;

            #{Text.$selector} {
                @include Color.set($fg: "primary");
                @include Font.set(
                    $size: gamma,
                    $weight: medium,
                    $family: "default",
                    $align: default
                );
            }
        }
    }

    .textBody {
        @include Margin.set($bottom: delta);

        #{Text.$selector} {
            @include Color.set($fg: "gray-dark");
            @include Font.set($size: delta, $weight: default, $family: "default", $align: default);
        }
    }

    .alignFooter {
        @include Flex.set($justify: center, $align: center);

        #{Text.$selector} {
            @include Margin.set($right: alpha);
            @include Color.set($fg: "gray-dark");
            @include Font.set($size: gamma, $weight: default, $family: "default", $align: default);
        }
    }

    @include screen.lg {
        .textBody {
            @include Margin.set($bottom: zeta);
        }
    }
}
