@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Element/Link";
@use "@app/bancor-components/Element/Image";
@use "@app/bancor-components/Style/screen";
@use "@app/bancor-components/Style/size";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Shadow";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Border";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Padding";
@use "@app/bancor-components/Style/Layout/Display";
@use "@app/bancor-components/Style/Layout/Position";

@use "." as this;

$container-padding-lg: 9.75rem;

#{this.$selector} {
    @include Margin.set($left: beta, $right: beta);

    & > article {
        width: 100%;
        height: 100vh;
        @include Display.set($display: grid);
        grid-template-rows: 10% auto 10%;
        grid-template-columns: 100%;
        grid-template-areas: "header" "section" "footer";
        &.hasLogo {
            height: calc(100vh - Layout.get-size(kappa));
        }

        header {
            grid-area: header;
            @include Position.set($position: relative);
            @include Flex.set($justify: center, $align: center);
            // TODO: Falta definición del banco para el color de este botón
            #{Button.$selector} {
                @include screen.sm {
                    @include Position.set($position: absolute, $left: none);
                }

                & > svg {
                    & > path {
                        @include Color.set($svg-fg: "gray-dark");
                    }
                }
            }

            #{Text.$selector} {
                @include Color.set($fg: "gray-dark");
                @include Font.set($size: delta, $weight: bold, $family: "heading", $align: default);
                width: 100%;
            }
        }

        header.onlyRow {
            @include Flex.set($justify: start);
        }

        section {
            grid-area: section;
            @include Flex.set($justify: start, $dir: column);

            & > figure {
                @include Margin.set($bottom: delta);
                @include Flex.set($justify: center);
            }
        }

        footer {
            grid-area: footer;
            @include Flex.set($justify: center, $align: center);

            #{Text.$selector} {
                @include Margin.set($right: alpha);
                @include Color.set($fg: "gray-dark");
                @include Font.set(
                    $size: gamma,
                    $weight: default,
                    $family: "default",
                    $align: default
                );
            }

            #{Link.$selector} {
                @include Font.set($deco: default);

                #{Text.$selector} {
                    @include Color.set($fg: "primary");
                    @include Font.set(
                        $size: gamma,
                        $weight: medium,
                        $family: "default",
                        $align: default
                    );
                }
            }
        }
    }

    @include screen.lg {
        @include Margin.set($left: auto, $right: auto);
        @include Padding.set(
            $left: $container-padding-lg,
            $right: $container-padding-lg,
            $top: zeta,
            $bottom: zeta
        );
        @include Border.set($radius: delta);
        @include Color.set($bg: gray-100);
        @include Shadow.set($box: gamma);
        @include Position.set($position: relative);
        width: size.$width-md;
        overflow-y: auto;
        top: Layout.get-max-height(alpha);

        header {
            @include Margin.set($bottom: delta);
            #{Button.$selector} {
                @include Position.set($position: initial);
            }
        }

        article {
            height: 100%;
            grid-template-rows: auto;

            &.hasLogo {
                height: inherit;
            }

            section {
                & > figure {
                    @include Margin.set($bottom: zeta);
                }
            }
        }
    }
}

.step1Logo {
    @include Flex.set($justify: end);
    background-image: url("../../Element/Image/assets/TramaMobile.svg");
    background-size: cover;
    height: Layout.get-size(kappa);
    width: Layout.get-width(kappa);
    #{Image.$selector} {
        @include Margin.set($right: delta);
        width: calc(Layout.get-size(beta) + Layout.get-size(delta));
        height: Layout.get-size(iota);
    }

    @include screen.lg {
        @include Position.set($position: absolute);
        width: calc(Layout.get-width(kappa) / 3.5);
        height: auto;
        background: none;
        #{Image.$selector} {
            @include Margin.set($right: none);
            width: calc(Layout.get-size(lambda) + Layout.get-size(gamma));
            height: calc(Layout.get-size(kappa) + Layout.get-size(beta));
        }
    }
}
